    padding: 0.25em;
    font-size: 1.2em;
    font-weight: 500;
}

.emptyHeader span {
    font-size: 1em;
    font-weight: 400;
}

.topPanelTitle {
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding-left: 1em;
}
